@import 'index';

#background {
  position: fixed;
  height: 100vh;
  width: 100vw;
  top: 0;
  left: 0;
  z-index: -2;
  background: var(--background-color);
  color: var(--text-color);
  transition-duration: .4s;
}

@keyframes noise {
  0% { transform: translate(20%,-15%); }
  10% { transform: translate(-20%,-15%); }
  20% { transform: translate(20%,-5%); }
  30% { transform: translate(-20%,-5%); }
  40% { transform: translate(20%,5%); }
  50% { transform: translate(-20%,5%); }
  60% { transform: translate(20%,15%); }
  70% { transform: translate(-20%,15%); }
  80% { transform: translate(20%,5%); }
  90% { transform: translate(-20%,5%); }
  100% { transform: translate(20%,-5%); }
}

#noise {
  background-repeat: repeat;
  position: fixed;
  z-index: 200;
  width: 300%;
  height: 300%;
  left: -100%;
  top: -100%;
  pointer-events: none;
  animation: noise 0.5s steps(10) infinite;
}

.hooks-main {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: -1;
}
.b1,
.b2,
.b3 {
  position: absolute;
  will-change: transform;
  border-radius: 50%;
  background: #d292f0;
  opacity: 0.6;
  box-shadow: 0 10px 10px rgba(0, 0, 0, 1);
}
.b1 {
  width: 170px;
  height: 170px;
}
.b2 {
  width: 350px;
  height: 350px;
}
.b3 {
  width: 200px;
  height: 200px;
}
.b1::after,
.b2::after,
.b3::after {
  content: '';
  position: absolute;
  top: 50px;
  left: 50px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 1);
}
.b2::after {
  top: 70px;
  left: 70px;
  width: 70px;
  height: 70px;
}
.b3::after {
  top: 50px;
  left: 50px;
  width: 50px;
  height: 50px;
}
.hooks-filter {
  position: absolute;
  width: 100%;
  height: 100%;
  filter: url('#goo');
  overflow: hidden;
}

.Hero {
  height: 100vh;
  width: 100%;
  top: 0;
  left: 0;
  margin: 0;
  padding: 40px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  .name, .bottom {
    width: 100%;
    overflow: hidden;
    font-size: 11vw;
    div {
      width: 100%;
      font-family: 'Dahlia';
      font-weight: 400;
      line-height: 0.55;
      padding-bottom: 2.8vw;
      transition: color .4s;
    }
  }
  .hidden {
    opacity: 0;
    pointer-events: none;
  }
  .link {
    position: fixed;
    top: 40px;
    left: 40px;
    z-index: 100;
  }
  .center {
    width: 100%;
    max-width: 400px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    p {
      text-align: center;
      font-size: 1.25rem;
      transition-duration: .4s;
      overflow: hidden;
    }
  }
  .bottom div {
      text-align: right;
      padding-bottom: 2vw;
  }
  
  .checkbox {
    display: none;
  }
  .label {
    transition-duration: .4s;
    height: 2.4rem;
    width: 8rem;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    border: 3px solid rgba(var(--text-color-rgb), 0.15);
    border-radius: 100vw;
    position: absolute;
    bottom: 40px;
    left: 40px;
    p {
      font-size: 1rem;
      text-align: left;
      transition-duration: .4s;
    }
    .ball {
      position: absolute;
      background: var(--text-color);
      width: 3.4rem;
      height: 1.4rem;
      border-radius: 100vw;
      transform: translateX(-1.8rem);
      transition-duration: .4s;
    }
    &:hover {
      cursor: pointer;
    }
  }
  .checkbox:checked ~ .label {
    .ball {
      transform: translateX(1.8rem);
    }
    .moon {
      opacity: 1;
    }
    .sun {
      opacity: 0;
    }
  }
}

.Work {
  width: calc(100% - 80px);
  margin: 0 40px;
  padding: 200px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-top: 2px solid var(--text-color);
  transition-duration: .4s;
  // .headshot {
  //   width: 100%;
  //   max-width: 600px;
  // }
  .big {
    font-size: 8vw;
    width: 100%;
    max-width: 1000px;
    text-align: center;
    line-height: 1;
    u {
      font-weight: 600;
    }
  }
}
